'use client';

import React, { useMemo, useState } from 'react';
import ExportedImage from 'next-image-export-optimizer';

const STAGE_W = 780;
const STAGE_H = 660;
const CX = Math.round(STAGE_W / 2);
const CY = Math.round(STAGE_H * 0.56);

const RINGS: readonly number[] = [320, 270, 220, 175, 135];

const GRAD_LINE = 'bg-gradient-to-r from-[#0D70DF] to-[#E63637]';
const GRAD_TEXT = 'text-transparent bg-clip-text bg-gradient-to-r from-[#0D70DF] to-[#E63637]';

const GradientBadge: React.FC<{ title: string }> = ({ title }) => (
  <span className={`text-[12px] font-semibold px-3 py-[6px] rounded-[8px] whitespace-nowrap text-white ${GRAD_LINE}`}>
    {title}
  </span>
);

/** ТОЛЬКО точка крутится по окружности.
 * Палочка и бейдж обёрнуты в контр-вращающуюся обёртку → всегда справа и горизонтально. */
const OrbitTrain: React.FC<{
  r: number;
  startDeg: number;
  period: number;
  title: string;
}> = ({ r, startDeg, period, title }) => {
  const DOT = 9;
  const CONNECTOR = 28;
  const GAP = 6;

  return (
    <div
      className="absolute pointer-events-none"
      style={{ left: CX, top: CY, transform: 'translate(-50%, -50%)' }}
      aria-hidden
    >
      {/* Вращаем якорь (угол + анимация) */}
      <div
        style={{
          transformOrigin: '0 0',
          transform: `rotate(${startDeg}deg)`,
          animation: `orbitCW ${period}s linear infinite`,
          willChange: 'transform',
        }}
      >
        {/* Выносим на радиус */}
        <div style={{ transform: `translateX(${r}px)` }}>
          {/* Нулевой контейнер — в нём точка + контр-вращающийся блок справа */}
          <div style={{ position: 'relative', width: 0, height: 0 }}>
            {/* ТОЧКА — едет по кольцу */}
            <span
              className={GRAD_LINE}
              style={{
                position: 'absolute',
                left: 0,
                top: 0,
                width: DOT,
                height: DOT,
                borderRadius: '9999px',
                transform: 'translate(-50%, -50%)',
                boxShadow: '0 0 0 2px rgba(255,255,255,0.85), 0 0 8px rgba(13,112,223,0.35)',
              }}
            />

            {/* КОНТР-ВРАЩЕНИЕ: всё, что справа от точки, остаётся горизонтальным */}
            <div
              style={{
                position: 'absolute',
                left: 0,
                top: 0,
                transform: `translateY(-50%) rotate(${-startDeg}deg)`,
                animation: `uprightCW ${period}s linear infinite`,
                transformOrigin: '0 50%',
                display: 'flex',
                alignItems: 'center',
                whiteSpace: 'nowrap',
                willChange: 'transform',
              }}
            >
              {/* зазор от точки */}
              <span style={{ width: DOT / 2 + GAP, height: 1 }} />
              {/* ПАЛОЧКА — всегда направо */}
              <span
                className={GRAD_LINE}
                style={{
                  width: CONNECTOR,
                  height: 2,
                  boxShadow: '0 0 6px rgba(13,112,223,0.25)',
                  flex: '0 0 auto',
                }}
              />
              {/* БЕЙДЖ — сразу за палочкой */}
              <span style={{ marginLeft: 6, display: 'inline-block' }}>
                <GradientBadge title={title} />
              </span>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

type CatKey = 'desktop' | 'group' | 'integration' | 'custom';

const CATEGORIES: {
  id: CatKey;
  label: string;
  mock1: string;
  mock2: string;
  paragraphs: string[];
}[] = [
  {
    id: 'desktop',
    label: 'Наши лагеря Desktop',
    mock1: '/images/orbit/desktop-back.png',
    mock2: '/images/orbit/desktop-front.png',
    paragraphs: [
      'Разработана специально для детских лагерей с учётом процессов.',
      'Онлайн-продажа путёвок, документооборот, аналитика.',
      'Гибкие отчёты и роли доступа.',
    ],
  },
  {
    id: 'group',
    label: 'Групповое бронирование',
    mock1: '/images/orbit/group-back.png',
    mock2: '/images/orbit/group-front.png',
    paragraphs: [
      'Массовое бронирование, квоты и быстрые заявки.',
      'Шаблоны групп, распределение по отрядам.',
      'Уведомления и статусы.',
    ],
  },
  {
    id: 'integration',
    label: 'Интеграция с 1С бухгалтерия',
    mock1: '/images/orbit/1c-back.png',
    mock2: '/images/orbit/1c-front.png',
    paragraphs: [
      'Экспорт проводок и платежей в 1С.',
      'Сверка и журналы операций.',
      'Автоматизация отчётности.',
    ],
  },
  {
    id: 'custom',
    label: 'Индивидуальный функционал',
    mock1: '/images/orbit/custom-back.png',
    mock2: '/images/orbit/custom-front.png',
    paragraphs: [
      'Кастомные модули под задачи лагеря.',
      'Гибкая настройка прав и интерфейсов.',
      'Интеграции с внешними системами.',
    ],
  },
];

export default function FeaturesOrbitExact(): JSX.Element {
  const [active, setActive] = useState<CatKey>('desktop');
  const cat = CATEGORIES.find(c => c.id === active)!;

  const trains = useMemo(
    () => [
      { title: 'СИСТЕМА АНАЛИТИКИ',                  r: RINGS[0], start: 200, period: 30 },
      { title: 'ФУНКЦИЯ ПОМОЩНИК ВОЖАТОГО',          r: RINGS[1], start: 235, period: 26 },
      { title: 'СОВРЕМЕННЫЙ ДОКУМЕНТООБОРОТ',        r: RINGS[2], start: 350, period: 22 },
      { title: 'ПЕРЕДОВАЯ СИСТЕМА ПРОДАЖ',           r: RINGS[3], start:  20, period: 20 },
      { title: 'МНОГОФУНКЦИОНАЛЬНЫЙ ЛИЧНЫЙ КАБИНЕТ', r: RINGS[4], start: 140, period: 18 },
    ],
    []
  );

  return (
    <section className="relative py-16 lg:py-24 overflow-hidden">
      <style>{`
        @keyframes orbitCW   { from { transform: rotate(0deg); } to { transform: rotate(360deg); } }
        @keyframes uprightCW { from { transform: rotate(0deg); } to { transform: rotate(-360deg); } }
      `}</style>

      <div className="container mx-auto px-4 lg:px-8">
        {/* шапка */}
        <div className="text-center mb-8">
          <div className="inline-flex items-center gap-2 px-3 py-1 rounded-full bg-blue-50 text-blue-600 text-sm font-medium mb-4">
            <i className="fa-solid fa-star" /> Переходите на новый уровень
          </div>

          <h2 className="font-bounded text-[34px] sm:text-[40px] md:text-[48px] font-extrabold leading-tight">
            ОСНОВНОЙ ФУНКЦИОНАЛ
            <span className="block">ПЛАТФОРМЫ</span>
          </h2>

          <p className="mt-4 text-[12px] tracking-wide text-gray-600">
            РАЗРАБОТАНА СПЕЦИАЛЬНО ДЛЯ ДЕТСКИХ ЛАГЕРЕЙ С УЧЁТОМ ВСЕХ ВАЖНЫХ АСПЕКТОВ
            <br />
            УПРАВЛЕНИЯ И ПРОДАЖ ПУТЁВОК
          </p>
        </div>

        <div className="grid lg:grid-cols-2 gap-8 items-start">
          {/* сцена */}
          <div className="relative">
            <div className="relative mx-auto" style={{ width: STAGE_W, height: STAGE_H }}>
              {active === 'desktop' && (
                <>
                  <svg
                    viewBox={`0 0 ${STAGE_W} ${STAGE_H}`}
                    className="absolute inset-0 w-full h-full pointer-events-none"
                  >
                    <defs>
                      <linearGradient id="ringGrad" x1="0" x2="1" y1="0" y2="1">
                        <stop offset="0%" stopColor="#0D70DF" />
                        <stop offset="100%" stopColor="#E63637" />
                      </linearGradient>
                    </defs>
                    {RINGS.map((r, i) => (
                      <circle
                        key={r}
                        cx={CX}
                        cy={CY}
                        r={r}
                        fill="none"
                        stroke="url(#ringGrad)"
                        strokeWidth={3 - i * 0.2}
                        strokeDasharray={i < 2 ? '12 16' : i < 4 ? '10 14' : '8 12'}
                        strokeOpacity={0.22 - i * 0.02}
                      />
                    ))}
                  </svg>

                  {trains.map((t, i) => (
                    <OrbitTrain key={i} r={t.r} startDeg={t.start} period={t.period} title={t.title} />
                  ))}
                </>
              )}

              {/* мокапы */}
              <div className="absolute" style={{ left: CX, top: CY + 20, transform: 'translate(-50%, -50%)' }}>
                <div className="absolute -left-28 top-10 rotate-[-6deg] scale-[0.92] opacity-95 z-0">
                  <ExportedImage
                    src={cat.mock1}
                    alt="Back tablet"
                    width={900}
                    height={900}
                    className="w-[440px] lg:w-[500px] h-auto drop-shadow-xl"
                  />
                </div>
                <div className="relative z-10 rotate-[8deg]">
                  <ExportedImage
                    src={cat.mock2}
                    alt="Front tablet"
                    width={900}
                    height={900}
                    className="w-[520px] lg:w-[580px] h-auto drop-shadow-2xl"
                  />
                </div>
              </div>
            </div>
          </div>

          {/* правая колонка */}
          <div className="relative mt-6 lg:mt-0">
            <div className="pl-0 lg:pl-16">
              <div className="relative">
                <span className={`absolute -left-4 top-2 bottom-2 w-[3px] rounded-full ${GRAD_LINE}`} />
                <h3 className="text-[26px] font-extrabold text-[#2D3B6F] mb-2">{cat.label}</h3>
                <div className="space-y-3 text-[14px] text-[#3F4B66] max-w-[520px]">
                  {cat.paragraphs.map((p, i) => (
                    <p key={i} className="leading-snug">{p}</p>
                  ))}
                </div>
                <div className="mt-5 flex items-center gap-3">
                  <span className={`block w-24 h-[2px] ${GRAD_LINE}`} />
                  <a href="#contact" className="text-[13px] font-semibold text-[#2D3B6F] hover:text-[#0D70DF] cursor-pointer">
                    УЗНАТЬ БОЛЬШЕ
                  </a>
                </div>
              </div>

              <div className="mt-8 space-y-4">
                {CATEGORIES.map((c) => (
                  <button
                    key={c.id}
                    onClick={() => setActive(c.id)}
                    className={`block text-left text-[22px] font-extrabold tracking-tight ${
                      active === c.id ? GRAD_TEXT : 'text-[#2D3B6F]'
                    }`}
                  >
                    {c.label}
                  </button>
                ))}
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
}
